import React, { Component } from 'react';
import {BrowserRouter as Router,Route,Switch} from 'react-router-dom';
import './App.css';
import Home from './components/Home/Home'
import BackDrop from './components/Backdrop/BackDrop';
import Toolbar from './components/Toolbar/Toolbar';
import SideDrawer from './components/SideDrawer/SideDrawer';
import Contact from './components/contact/Contact';
import Gallary from './components/Gallary/Gallary';
import About from './components/About/About';
import Sponsors from './components/Sponsor/Sponsors';

class App extends Component {
  state = {
    sideDrawerOpen: false
  };

  drawerToggleClickHandler = () => {
    this.setState((prevState) => {
      return { sideDrawerOpen: !prevState.sideDrawerOpen };
    });
  };
  backdropClickHandler = () => {
    this.setState({ sideDrawerOpen: false });
  };




  render() {
    let backdrop;
    if (this.state.sideDrawerOpen) {
      backdrop = <BackDrop click={this.backdropClickHandler} />
    }
    return (
      <div className="App">
        <Router>
        <Toolbar drawerClickHandler={this.drawerToggleClickHandler} />
        <SideDrawer show={this.state.sideDrawerOpen} />
        {backdrop}
        <Route exact path='/techFest20' component={Home} />
        <Route exact path='/techFest20/sponsors' component={Sponsors} />
        <Route exact path='/techFest20/contact' component={Contact} />
        <Route exact path='/techFest20/gallary' component={Gallary} />
        <Route exact path='/techFest20/about' component={About} />
        </Router>
      </div>

    );
  }
}

export default App;
